  font-weight: normal;
  text-decoration: none;
}

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
  font-family: "Oswald", "Arial", "Helvetica", sans-serif;
}

h1,
h2,
h3,
h4,
h5,
h6 {
