.main-nav {
  position: relative;
  width: 100%;
  min-height: 1px;
  margin: 0 auto;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .main-nav {
    width: auto;
    margin: 0;
  }
}

.main-nav__toggle {
  position: absolute;
  content: "";
  top: -64px;
  right: 0;
  z-index: 2;
  display: block;
  width: 64px;
  height: 64px;
  background-color: #ffffff;
  cursor: pointer;
  border: none;
}
.main-nav__toggle:hover {
  opacity: 0.7;
}
.main-nav__toggle:active {
  opacity: 0.4;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}

.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 23px;
  left: 22px;
  width: 24px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
}

.main-nav--closed .main-nav__toggle:active::before {
  opacity: 0.3;
}

.main-nav--opened .main-nav__toggle {
  top: -64px;
  right: 0;
  left: auto;
}
.main-nav--opened .main-nav__toggle::before, .main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 30px;
  right: 22px;
  width: 24px;
  height: 2px;
  background-color: #000000;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav--opened .main-nav__toggle:active::before, .main-nav--opened .main-nav__toggle:active::after {
  opacity: 0.3;
}

.main-nav__list {
  margin: 0 auto;
  padding: 0;
  list-style: none;
}

@media (max-width: 767px) {
  .main-nav--closed .site-list__item {
    display: none;
  }

  .main-nav--closed .site-list__item--active a {
    padding-right: 70px;
    padding-left: 70px;
    text-align: center;
  }

  .main-nav--opened .main-nav__wrapper {
    position: initial;
    top: 0;
    right: 0;
    left: 0;
    z-index: 10;
    background-color: #ffffff;
  }

  .main-nav--opened .main-nav__wrapper::before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    z-index: 2;
    width: 7px;
    height: 100%;
  }

  .main-nav--nojs .main-nav__toggle {
    display: none;
  }

  .main-nav--nojs .main-nav__wrapper {
    position: static;
    min-height: 0;
  }

  .main-nav--nojs .site-list__item {
    display: block;
  }

  .main-nav--nojs .site-list__item--active a {
    padding-right: 22px;
    padding-left: 22px;
    text-align: center;
  }
}
@media (min-width: 768px) {
  .main-nav__wrapper {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 440px;
    /*  440px / 768px ~= 57% */
    max-width: 480px;
    margin: 0;
    margin-right: 55px;
  }
}
@media (min-width: 1300px) {
  .main-nav__wrapper {
    margin-top: 134px;
  }
}

