  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
}
@media (min-width: 768px) {
  .cat-info__item {
    margin-bottom: 20px;
  }
}

@media (min-width: 1300px) {
  .cat-info__item:last-child {
    margin-bottom: 0;
  }
}

.cat-info__item > input {
  max-width: 100%;
  box-sizing: border-box;
  margin-left: 15px;
  padding: 12px;
  padding-left: 15px;
  flex: 1;
  border: 1px solid #e6e6e6;
}
@media (min-width: 768px) {
  .cat-info__item > input {
    padding: 13px 20px 15px;
  }
}

.cat-info__item input,
.user-info__item input,
.comment textarea {
  text-transform: uppercase;
  color: #444444;
}

.cat-info__item input:hover,
.user-info__item input:hover,
.comment textarea:hover {
  border: 1px solid #cdcdcd;
}

.cat-info__item input:focus,
.user-info__item input:focus,
.comment textarea:focus {
  border: 1px solid #444444;
}

.cat-info__item input:invalid,
.user-info__item input:invalid {
  border: 1px solid #ff8282;
}

.user__input {
  padding: 13px 45px 13px 15px;
  background-size: 18px 20px;
}

.comment textarea {
  width: 100%;
  height: 150px;
  margin-top: 14px;
  padding: 14px 15px;
  resize: none;
  box-sizing: border-box;
}
@media (min-width: 768px) {
  .comment textarea {
    height: 174px;
  }
}

.user-info__title,
.comment__title,
.filter-extra__title {
  font-family: "Oswald", "Arial", "Helvetica", sans-serif;
  font-size: 24px;
  line-height: 24px;
  text-transform: none;
  z-index: 2;
  position: relative;
}
@media (min-width: 768px) {
  .user-info__title,
.comment__title,
.filter-extra__title {
    margin-bottom: 70px;
    font-size: 36px;
    line-height: 36px;
  }
}

.user-info,
.comment,
.filter-extra {
  position: relative;
}

.user-info::after {
  content: "";
  position: absolute;
  top: 43px;
  right: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 3px;
  background-color: #68b738;
}
@media (min-width: 768px) {
  .user-info::after {
    top: 60px;
  }
}
@media (min-width: 1300px) {
  .user-info::after {
    top: 90px;
  }
}

@media (min-width: 768px) {
  .user-info__wrapper {
    padding: 0 40px;
  }
}
@media (min-width: 1300px) {
  .user-info__wrapper {
    display: flex;
    justify-content: space-between;
    padding: 0;
  }
}

.comment::after {
  content: "";
  position: absolute;
  top: 32px;
  right: 0;
  z-index: -1;
  width: 100%;
  height: 3px;
  background-color: #68b738;
}
@media (min-width: 768px) {
  .comment::after {
    top: 80px;
    left: 0;
    width: 100%;
  }
}
@media (min-width: 1300px) {
  .comment::after {
    width: 100%;
  }
}

.comment__title {
  display: inline-block;
  vertical-align: top;
  width: 120px;
  margin: 18px auto 20px -20px;
  padding: 0 20px;
  background-color: #ffffff;
  background-position: 0 0;
  background-size: auto;
}
@media (min-width: 768px) {
  .comment__title {
    width: 180px;
    margin-top: 58px;
    margin-bottom: 62px;
    margin-left: 0;
    padding-right: 37px;
    padding-left: 37px;
  }
}
@media (min-width: 1300px) {
  .comment__title {
    margin-left: -40px;
    padding-left: 40px;
  }
}

@media (min-width: 768px) {
  .comment__wrapper {
    padding: 0 40px;
  }
}
@media (min-width: 1300px) {
  .comment__wrapper {
    padding: 0;
  }
}

.user-info__title {
  display: inline-block;
  vertical-align: top;
  width: 185px;
  margin: 30px auto 23px -20px;
  padding: 0 20px;
  background-color: #ffffff;
  background-position: 0 0;
  background-size: auto;
}
@media (min-width: 768px) {
  .user-info__title {
    width: 515px;
    margin-top: 37px;
    margin-bottom: 70px;
    margin-left: 0;
    padding-right: 37px;
    padding-left: 37px;
  }
}
@media (min-width: 1300px) {
  .user-info__title {
    margin-top: 68px;
    margin-bottom: 68px;
    margin-left: -40px;
    padding-left: 40px;
  }
}

.user-info__item {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 1300px) {
  .user-info__item {
    width: 570px;
    margin: 0;
    padding: 0;
  }
}

.user-info__item-inner {
  flex: 1;
  position: relative;
  width: auto;
}
.user-info__item-inner > svg {
  position: absolute;
  top: 50%;
  right: 18px;
  transform: translateY(-50%);
}
.user-info__item-inner > input {
  width: 100%;
  max-width: calc(100% - 10px);
  box-sizing: border-box;
  border: 1px solid #e6e6e6;
  margin-top: 8px;
  margin-bottom: 8px;
  margin-left: 10px;
}
@media (min-width: 768px) {
  .user-info__item-inner > input {
    max-width: calc(100% - 15px);
    margin-top: 9px;
    margin-bottom: 9px;
    margin-left: 20px;
  }
}

.user__input:focus + .user__email--icon,
.user__input:focus + .user__phone--icon {
  fill: #68b738;
}

.user__input:invalid + .user__email--icon,
.user__input:invalid + .user__phone--icon {
  fill: #ff8282;
}

.filter-extra__title {
  display: inline-block;
  vertical-align: top;
  width: 140px;
  margin: 28px auto 18px -20px;
  padding: 0 20px;
  background-color: #ffffff;
  background-position: 0 0;
  background-size: auto;
}
@media (min-width: 768px) {
  .filter-extra__title {
    width: 210px;
    margin-top: 65px;
    margin-bottom: 36px;
    margin-left: 0;
    padding-left: 37px;
    padding-right: 37px;
  }
}
@media (min-width: 1300px) {
  .filter-extra__title {
    margin-left: -40px;
    padding-left: 40px;
  }
}

.filter-extra::after {
  content: "";
  position: absolute;
  top: 42px;
  right: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 3px;
  background-color: #68b738;
}
@media (min-width: 768px) {
  .filter-extra::after {
    top: 86px;
  }
}

.program__filter {
  margin-left: -20px;
  margin-right: -20px;
  padding-left: 20px;
  padding-right: 20px;
  border-top: 1px solid #ededed;
  border-bottom: 1px solid #ededed;
}
@media (min-width: 768px) {
  .program__filter {
    margin-right: 0;
    margin-bottom: 37px;
    margin-left: 0;
    border-right: 1px solid #ededed;
    border-left: 1px solid #ededed;
  }
}
@media (min-width: 1300px) {
  .program__filter {
    width: 570px;
    margin: 0 0 0 40px;
    padding: 0;
  }
}

.filter-options {
  padding: 0;
  list-style: none;
}

@media (min-width: 768px) {
  .filter-option {
    margin-bottom: 4px;
  }
}
@media (min-width: 1300px) {
  .filter-option {
    padding-left: 22px;
  }
}

.filter-option__input--radio {
  display: none;
}

.filter-option__label--radio {
  position: relative;
  display: block;
  margin-bottom: 5px;
  margin-left: 0;
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 45px;
}
@media (min-width: 768px) {
  .filter-option__label--radio {
    padding-top: 14px;
    padding-bottom: 14px;
    padding-left: 65px;
  }
}

.filter-option__label--radio:active {
  opacity: 0.3;
}

.filter-option__label--radio::before {
  content: "";
  position: absolute;
  left: 0;
  width: 10px;
  height: 10px;
  margin-top: -6px;
  background-color: #ffffff;
  border: 9px solid #ffffff;
  box-shadow: 0 0 0 1px #cecece;
  border-radius: 50%;
}
@media (min-width: 768px) {
  .filter-option__label--radio::before {
    top: 14px;
    left: 9px;
    width: 20px;
    height: 20px;
  }
}

.filter-option__input:checked + .filter-option__label--radio::after {
  content: "";
  position: absolute;
  top: 12px;
  left: 8px;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  border: none;
  background: #68b738;
  margin-top: 0;
}
@media (min-width: 768px) {
  .filter-option__input:checked + .filter-option__label--radio::after {
    top: 18px;
    left: 19px;
    width: 18px;
    height: 18px;
  }
}

.filter-extra__list {
  padding: 0;
  padding-bottom: 8px;
  list-style: none;
}
@media (min-width: 768px) {
  .filter-extra__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-start;
    padding: 0 40px;
  }
}
@media (min-width: 1300px) {
  .filter-extra__list {
    padding: 0;
  }
}

.filter-extra__item {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}
@media (min-width: 768px) {
  .filter-extra__item {
    width: 45%;
    margin-bottom: 32px;
  }
}
@media (min-width: 1300px) {
  .filter-extra__item {
    width: 245px;
  }
}

.filter-extra__input--checkbox {
  display: none;
}

.filter-extra__label {
  position: relative;
  display: block;
  margin-left: 0;
  padding-top: 7px;
  padding-bottom: 7px;
  padding-left: 45px;
}
@media (min-width: 768px) {
  .filter-extra__label {
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 58px;
  }
}

.filter-extra__label::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  display: block;
  width: 28px;
  height: 28px;
  border: 1px solid #ededed;
}
@media (min-width: 768px) {
  .filter-extra__label::before {
    width: 37px;
    height: 37px;
  }
}

.filter-extra__input:checked + .filter-extra__label::before {
  content: "";
  position: absolute;
  top: 3px;
  left: 0;
  background-image: url("../../img/icon-tick.svg");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 75%;
}

.filter-extra__button {
  margin-bottom: 18px;
  padding-top: 11px;
  padding-bottom: 11px;
  line-height: 16px;
}
@media (min-width: 768px) {
  .filter-extra__button {
    margin-top: 2px;
    margin-right: 70px;
    margin-left: 0;
    padding: 16px;
    font-size: 20px;
    line-height: 20px;
  }
}
@media (min-width: 1300px) {
  .filter-extra__button {
    width: 570px;
    margin-top: 10px;
    margin-right: 40px;
    padding-top: 16px;
    padding-bottom: 16px;
  }
}

.program-form__legend {
  padding-left: 62px;
  font-family: "Arial", "Helvetica", sans-serif;
  font-size: 14px;
  line-height: 16px;
  text-transform: none;
}
@media (min-width: 768px) {
  .program-form__legend {
    padding-top: 17px;
    padding-left: 0;
    font-size: 16px;
    line-height: 16px;
  }
}
@media (min-width: 1300px) {
  .program-form__legend {
    margin-left: 40px;
    padding-top: 25px;
  }
}

@media (min-width: 768px) {
  .filter-extra__request {
    display: flex;
    padding: 0 40px;
  }
}
@media (min-width: 1300px) {
  .filter-extra__request {
    padding: 0;
  }
}